@import 'editor/trumbowyg.giphy';
@import 'editor/trumbowyg.colors';
// prism
@import 'prism/prism';
@import 'prism/prism-line-highlight';

.pob-d-flex-1014-wgstzux {
  display: flex;
}

@media screen and (max-width: 992px) {
  .pob-d-flex-1014-wgstzux {
    display: block;
  }
}

// general
code {
  color: rgb(163, 19, 19);
}

pre {
  color: rgb(163, 19, 19);
  background-color: #e7e7e7;
  padding: 0.2rem 0;
}

blockquote {
  background-color: #ececec;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
}

.pob-text-white {
  color: #fff;
}

.pob-d-flex {
  display: flex;
}
.pob-d-block {
  display: block;
}

.pob-text-center {
  text-align: center;
}

.pob-text-white {
  color: #fff;
}

.pob-rounded {
  border-radius: 0.3rem !important;
}

.pob-rounded-top {
  border-top-left-radius: 0.375rem !important;
  border-top-right-radius: 0.375rem !important;
}

.pob-w-100 {
  width: 100%;
}

.pob-cursor-pointer {
  cursor: pointer;
}

// padding margin
.pob-m-1 {
  margin: 0.25rem !important;
}
.pob-me-1 {
  margin-right: 0.25rem !important;
}

.pob-me-2 {
  margin-right: 0.5rem !important;
}

.pob-me-3 {
  margin-right: 0.725rem !important;
}

.pob-mb-1 {
  margin-bottom: 0.25rem !important;
}

.pob-mb-2 {
  margin-bottom: 0.5rem !important;
}

.pob-mb-3 {
  margin-bottom: 0.725rem !important;
}

.pob-mt-2 {
  margin-top: 0.5rem !important;
}

.pob-pb-3 {
  padding-bottom: 1rem !important;
}

.pob-p-1 {
  padding: 0.5rem !important;
}

.pob-p-2 {
  padding: 1rem !important;
}

.pob-p-3 {
  padding: 1.5rem !important;
}

.pob-py-1 {
  padding-top: 0.25rem !important;
  padding-bottom: 0.25rem !important;
}

.pob-py-2 {
  padding-top: 0.5rem !important;
  padding-bottom: 0.5rem !important;
}

.pob-py-3 {
  padding-top: 0.725rem !important;
  padding-bottom: 0.725rem !important;
}

.pob-px-1 {
  padding-left: 0.25rem !important;
  padding-right: 0.25rem !important;
}

.pob-px-2 {
  padding-left: 0.5rem !important;
  padding-right: 0.5rem !important;
}

.pob-px-3 {
  padding-left: 0.725rem !important;
  padding-right: 0.725rem !important;
}

.pob-px-3 {
  padding-right: 1rem !important;
  padding-left: 1rem !important;
}

.pob-font-family {
  font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', 'Noto Sans', 'Liberation Sans', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
}

.pob-justify-content-between {
  justify-content: space-between;
}

.pob-border {
  border: 1px solid #cecece !important;
}

// btn
.pob-btn {
  padding: 0.375rem 0.75rem 0.475rem !important;
  font-weight: 400;
  line-height: 1.5;
  border-width: 0.375rem;
  border-color: transparent;
  display: inline-block;
  font-family: system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', 'Noto Sans', 'Liberation Sans', Arial, sans-serif, 'Apple Color Emoji',
    'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  border: 0 solid transparent;
  background-color: transparent;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.pob-btn-sm {
  padding-left: 0.5rem !important;
  padding-right: 0.5rem !important;
  padding-top: 0.2rem !important;
  padding-bottom: 0.1rem !important;
  border: 0 solid transparent;
}

.pob-btn-icon {
  padding: 0.3rem 0.5rem !important;
}

.pob-btn-secondary {
  background-color: #565e64;
  color: #fff;
  border-radius: 0.3rem;
  border: 1px solid #c9c9c9;
  padding-left: 0.5rem !important;
  padding-right: 0.5rem !important;
  padding-top: 0.175rem !important;
  padding-bottom: 0.175rem !important;
  &:hover {
    background-color: #3c4146;
  }
}

.pob-btn-success {
  background-color: #198754;
  color: #fff;
  &:hover {
    background-color: #146c43;
  }
}

.pob-btn-dark {
  background-color: #212529;
  &:hover {
    background-color: #424649;
  }
}

.pob-btn-light {
  background-color: #f8f9fa;
  color: #2c2c2c;
  &:hover {
    background-color: #ebebeb;
  }
}

.pob-btn-rounded-left {
  border-top-left-radius: 0.375rem;
}
.pob-btn-rounded-right {
  border-top-right-radius: 0.375rem;
}

// forms
.pob-form-control {
  display: block;
  padding: 0.375rem 0.75rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #c5c7c9;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-family: inherit;
  outline: 0;
}

.pob-form-control:disabled {
  background-color: #dadada;
}

.pob-form-control-sm {
  padding: 0.25rem 0.5rem;
  border-radius: 0.3rem;
  font-size: 1rem;
}

.pob-font-size-sm {
  font-size: 0.75rem;
}

.pob-form-control-color {
  width: 3rem;
  padding: 0.2rem;
  cursor: pointer;
  height: 30px !important;
}

.pob-form-control-number {
  padding: 0.25rem;
  width: 56px;
  outline: 0;
  border-radius: 0;
  text-align: right;
}

.pob-textarea {
  height: 30px;
  font-family: inherit;
  width: -webkit-fill-available;
  border-radius: 0.3rem;
  font-weight: 400;
  line-height: 1.5;
  font-family: inherit;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  resize: vertical;
}
.pob-form-check {
  display: block;
  padding-left: 1.5em;
  margin-bottom: 0.125rem;
  font-family: inherit;
}

.pob-form-check-input {
  width: 1.2em;
  height: 1.2em;
  margin-top: 0.25em;
  vertical-align: top;
  background-color: #fff;
  background-size: contain;
  border: 1px solid #cacaca;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -webkit-print-color-adjust: exact;
  outline: 0;
}

.pob-form-check-input:checked {
  border: 5px solid #0d6efd;
}

.pob-form-check-input:disabled {
  background-color: #dadada;
}

.pob-form-check-input[type='radio'] {
  border-radius: 50%;
}

.pob-form-control[type='number'] {
  font-size: 0.75rem;
}

.pob-form-check .pob-form-check-input {
  float: left;
  margin-left: -1.5em;
}

.pob-form-select {
  display: block;
  width: 100%;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: right 0.75rem center;
  background-size: 16px 12px;
  border: 1px solid #dee2e6;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  font-family: inherit;
  outline: 0;
}

.pob-form-select-sm {
  padding-top: 0.15rem;
  padding-bottom: 0.15rem;
  padding-left: 0.5rem;
  font-size: 0.875rem;
}

.pob-label {
  font-weight: bold;
  margin-top: 0.5rem;
  font-size: 14px;
}

.pob-spinner-loading {
  width: 40px;
  height: 40px;
  border: 8px solid #3f3f3f;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: pob-rotation 1s linear infinite;
}

@keyframes pob-rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

// Editor default
.pob-edit-default-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 0 20px;
}

.pob-edit-default-label-grid {
  font-size: 14px;
  margin-top: 0px;
}

// Output
.pob-container-content-output-small-screen {
  border: 1px solid #999;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  overflow-y: auto;
}

// Audio Editor
.pob-container-editor {
  background-color: transparent;
  position: relative;
  border-radius: 0 0 4px 4px;
  color: #000;
  height: 100%;
}
.pob-edit-section {
  display: block;
  background-color: #fff;
}

.pob-width-100-height-auto {
  width: 100%;
  height: auto;
}
.pob-audio {
  width: 100%;
}

.pob-justify-content-center {
  justify-content: center;
}

// TextEditor
.pob-text-editor-container,
.trumbowyg-editor {
  width: 100%;
  resize: vertical;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: #999 #333;
  box-sizing: border-box;
  border-radius: 0 0 4px 4px;
}

.text-editor-container::-webkit-scrollbar {
  width: 12px;
}

.text-editor-container::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.text-editor-container::-webkit-scrollbar-thumb {
  background: #888;
}

.text-editor-container::-webkit-scrollbar-thumb:hover {
  background: #555;
}

// HTMLeditor
.pob-textarea-html {
  height: 160px;
  width: -webkit-fill-available;
  resize: vertical;
}

// YoutubeEditor
.pob-iframe-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 25px;
  height: 0;
}
.pob-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

// FrissBee Player
.play-list::before {
  content: '[{bandName: Band Name 1, songTitle: Song 1,songFile: https://audio-player.frissbee.de/audio/Song_1.mp3,songImage: https://audio-player.frissbee.de/images/Song_1.jpg}]';
}

// Page Builder
.pob-loading {
  display: block;
  font-size: 28px;
  font-family: system-ui;
}

// SingleRow
.pob-settings-single-row {
  width: 100%;
  background-color: #7a4444;
}

// SingleColumn
single-column {
  box-sizing: border-box;
}
.pob-container-single-column {
  width: 100%;
  height: 100%;
  border: 1px solid #424242;
  position: relative;
}
.pob-settings-single-column,
.pob-section-edit-column {
  background-color: #225c85;
}

// SingleCell
.pob-container-info-cell {
  border: 1px solid #424242;
}

// Dropdown
.pob-dropdown {
  display: none;
  position: relative;
}
.pob-dropdown ul {
  padding: 0;
  margin: 0;
  width: auto;
}

.pob-dropdown-button {
  display: block;
  background-color: #f0f0f0;
  cursor: pointer;
  border: 1px solid #ccc;
  text-align: center;
}

.pob-dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 999;
  margin: 0;
}

.pob-dropdown-content li {
  list-style: none;
  padding: 0.5rem;
  width: 100%;
}

// SingleCell
.pob-container-info {
  background-color: #fff;
  font-size: 12px;
  color: #000;
}

.container-info-cell {
  border: 1px solid #424242;
  width: 100%;
  border-radius: 4px;
}
.pob-container-single-cell,
.pob-section-edit-cell {
  background-color: #3e7315;
}

.label-input {
  font-size: 14px;
  margin-top: 0px;
}

.d-flex-767 {
  display: flex;
}

.container-row {
  box-sizing: border-box;
  width: 100%;
  position: relative;
  border: 1px solid #424242;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.pob-btn-add {
  background-color: #85cef0;
  color: #333;
  &:hover {
    background-color: #75b6d4;
    color: #333;
  }
}

.pob-btn-edit {
  background-color: #288a30;
  color: #ffffff;
  &:hover {
    background-color: #1e6624;
    color: #ffffff;
  }
}

@media screen and (max-width: 767px) {
  .d-flex-767 {
    display: block;
    width: 100%;
  }
}
